import { Hidden, Stack, Typography } from "@mui/material";
import React from "react";
import { cleanInput } from "../utils";

let DisplayLine = ({ text, isError = false }) => {
  const sx = {
    fontWeight: "bold",
    fontFamily: "monospace",
    textAlign: "right",
    fontSize: getFontSize(text),
    overflowX: "hidden",
  };
  if (isError) {
    sx.color = "red";
  }
  return (
    <Typography sx={sx}>
      &nbsp;
      {text}
    </Typography>
  );
};
let Display = ({ input }) => {
  let result;
  let cleanedInput = cleanInput(input);
  try {
    result =
      isNaN(cleanedInput) && ![Infinity, NaN].includes(eval(cleanedInput))
        ? eval(cleanedInput).toString()
        : "";
  } catch (error) {
    result = "Format Error";
  }
  let isError = result === "Format Error";
  return (
    <Stack
      sx={{
        backgroundColor: "#f2f2f2",
        borderRadius: "45px",
      }}
    >
      <DisplayLine text={input} isError={isError} />
      <DisplayLine text={result} isError={isError} />
    </Stack>
  );
};
export default Display;

function getFontSize(input) {
  return (
    Math.floor(
      Math.max(Math.min(60, 60 - (20 * (input.length - 17)) / 9), 40)
    ) + "px"
  );
}
