import { Padding } from "@mui/icons-material";
import { Box, Grid } from "@mui/material";
import React from "react";

let Buttons = (props) => {
  const buttons = [
    "C",
    "( )",
    "^",
    "/",
    "7",
    "8",
    "9",
    "x",
    "4",
    "5",
    "6",
    "-",
    "1",
    "2",
    "3",
    "+",
    "0",
    ".",
    "",
    "=",
  ];
  const handleClick = ({ target }) => {
    props.handleButtonClick(target.id);
  };
  return (
    <Grid
      container
      sx={{
        height: { xs: "500px", sm: "700px" },
        padding: "10px",
        justifyContent: "space-between",
      }}
    >
      {buttons.map((value) => (
        <Grid
          item
          xs={3}
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Box
            onClick={handleClick}
            id={value}
            key={value}
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              width: { xs: "55px", sm: "80px" },
              height: { xs: "55px", sm: "80px" },
              padding: "10px",
              border: "5px solid black",
              backgroundColor: "#f1f1f1",
              fontSize: { xs: "50px", sm: "75px" },
              borderRadius: { xs: "30px", sm: "40px" },
              fontWeight: "bold",
            }}
          >
            {value === "x" ? (
              <div style={{ marginTop: "-17px" }}>{value}</div>
            ) : (
              value
            )}
          </Box>
        </Grid>
      ))}
    </Grid>
  );
};
export default Buttons;
