import React, { useState, useEffect } from "react";
import Buttons from "./Buttons";
import Display from "./Display";
import { cleanInput, netParentheses } from "../utils";
import { Stack } from "@mui/system";

let Calculator = () => {
  const [input, setInput] = useState("");
  useEffect(() => {
    const handleKeyDown = ({ key }) => {
      let result = key;
      switch (key) {
        case "Backspace":
          result = "";
          break;
        case "(":
        case ")":
          result = "( )";
          break;
        case "c":
          result = "C";
          break;
        case "*":
        case "X":
          result = "x";
          break;
        case "Enter":
          result = "=";
          break;
      }
      handleButtonClick(result);
    };
    document.addEventListener("keydown", handleKeyDown);
    return () => document.removeEventListener("keydown", handleKeyDown);
  });
  function handleButtonClick(clickedButton) {
    if (input.length > 25 && !"C=".includes(clickedButton)) return;
    if (clickedButton >= "0" && clickedButton <= "9") {
      setInput((prevInput) => {
        return (
          prevInput + (prevInput.slice(-1) === ")" ? "x" : "") + clickedButton
        );
      });
      return;
    }
    switch (clickedButton) {
      case "x":
      case "/":
      case "+":
      case "^":
        if (
          !input ||
          input.slice(-1) === "-" ||
          (input.slice(-1) === "e" && clickedButton !== "+") ||
          input === "." ||
          (input.slice(-1) === "." && isNaN(input.slice(-2, -1)))
        )
          return;
        if (["x", "/", "+", "^"].includes(input.slice(-1))) {
          setInput((prevInput) => prevInput.slice(0, -1) + clickedButton);
          return;
        } else {
          setInput((prevInput) => prevInput + clickedButton);
        }
        break;
      case "-":
        if (
          input === "." ||
          (input.slice(-1) === "." && isNaN(input.slice(-2, -1))) ||
          input.slice(-1) === "-"
        )
          return;
        setInput(
          (prevInput) =>
            prevInput + ("x+/^".includes(prevInput.slice(-1)) ? " " : "") + "-"
        );
        break;
      case "=":
        const result = eval(cleanInput(input)).toString();
        if (result === "Infinity" || result === "NaN") break;
        if (input && isNaN(cleanInput(input))) {
          setInput((prevInput) => {
            let result;
            try {
              result = eval(cleanInput(prevInput)).toString();
            } catch (error) {
              result = "Format Error";
            }
            return result;
          });
        }
        break;
      case "C":
        setInput("");
        break;
      case "":
        setInput((prevInput) =>
          [" -", "x("].includes(prevInput.slice(-2)) ||
          (prevInput.slice(-3, -1) === ")x" &&
            /[0123456789.]/.test(prevInput.slice(-1)))
            ? prevInput.slice(0, -2)
            : prevInput.slice(0, -1)
        );
        break;
      case "( )":
        if (
          input === "." ||
          input.slice(-1) === "e" ||
          (input.slice(-1) === "." && isNaN(input.slice(-2, -1)))
        )
          return;
        if (
          netParentheses(input) &&
          !["x", "/", "+", "-", "(", "^"].includes(input.slice(-1))
        ) {
          setInput((prevInput) => prevInput + ")");
        } else {
          setInput((prevInput) => {
            return prevInput + (/[0123456789)]$/.test(prevInput) ? "x(" : "(");
          });
        }
        break;
      case ".":
        if (!"e.".includes(input.slice(-1)) || !input) {
          setInput((prevInput) => {
            return (
              prevInput +
              (prevInput.slice(-1) === ")" ? "x" : "") +
              clickedButton
            );
          });
        }
        break;
    }
  }
  return (
    <Stack
      sx={{
        maxWidth: "625px",
        padding: "10px",
        backgroundColor: "gray",
      }}
    >
      <Display input={input} />
      <Buttons handleButtonClick={handleButtonClick} />
    </Stack>
  );
};
export default Calculator;
