export function cleanInput(input) {
  input = input.replace(/e[+-]?$/, ""); //removes 'e', 'e+' or 'e-' at the end
  input = input.replace(/[x/+\- (.^]*$/, ""); // removes any operants, spaces, parentheses and/or decimal buttons at the end
  input = input.replace(/x/g, "*");
  input = input.replace(/\^/g, "**");
  input = input.replace(/(?<=[^0-9.])0+(?=[0-9])/g, ""); // removes any unecessary 0s
  input = input.replace(/^0+(?=[0-9])/, ""); // removes any unecessary 0s at the beginning of the input
  input += ")".repeat(netParentheses(input));
  return input;
}

export function netParentheses(input) {
  let openCount = 0;
  let closedCount = 0;
  for (let char of input) {
    switch (char) {
      case "(":
        openCount++;
        break;
      case ")":
        closedCount++;
        break;
    }
  }
  return openCount - closedCount;
}
